	import notList from "@/components/notList.vue";
	import UserAttention from "./userAttention-model.js";
	const userAttention = new UserAttention();
	export default {
		components: {
			notList
		},
		data() {
			return {
				userInfo: {},
				attentionNode: {
					page: 1,
					data: []
				},
        originalPage: 0
			};
		},
		onLoad() {
			// const that = this
			// that._onLoad()
		},
		onShow() {
			const that = this
			that._onLoad()
		},
		methods: {
			_onLoad(callBack) {
				const that = this
				that.userInfo = that.$store.state.userInfo;
				that.getList(() => {
					callBack && callBack();
				})
			},
			// 加载列表
			getList(callBack) {
				const that = this
        if (that.originalPage == that.attentionNode.page) {
          return false
        }
				userAttention.getList({
					page: that.attentionNode.page || 1,
					openid: that.userInfo.openid
				}, (res) => {
					// console.log(res)
					if (res.code == 4000) {
            that.originalPage = that.attentionNode.page
						res.data = that.attentionNode.data.concat(res.data);
						that.attentionNode = res
					}
					callBack && callBack();
				})
			},
			// // 进入--客户经理--详情页
			goManagerDetails(e) {
				const that = this;
				const managerindex = userAttention.get_data_set(e, "managerindex");
				let data = JSON.stringify(that.attentionNode.data[managerindex]);
				userAttention.navigate_to(`/pages/managerDetails/managerDetails?data=${data}`);
			},
			// 取消关注
			notAttention(e) {
				const that = this
				uni.showModal({
					title: '系统提示',
					content: '确定取消关注？',
					success: function(res) {
						if (res.confirm) {
							// console.log('用户点击确定');
							let id = userAttention.get_data_set(e, "id")
							let index = userAttention.get_data_set(e, "index")
							userAttention.runNotAttention({
								openid: that.userInfo.openid,
								id: id
							}, (res) => {
								if (res.code == 4000) {
									userAttention.show_tips(res.explain)
									that.attentionNode.data = userAttention.delete_arr_index(that.attentionNode.data, index)
								} else {
									userAttention.show_tips(res.explain)
								}
								// callBack && callBack();
							})
						} else if (res.cancel) {
							// console.log('用户点击取消');
						}
					}
				});
			},
		},
		// 下拉刷新
		onPullDownRefresh() {
			var that = this;
      that.originalPage = 0
			that.attentionNode = {
				page: 1,
				data: []
			}
			that._onLoad(() => {
				uni.stopPullDownRefresh();
			});
		},
		//上拉加载更多
		onReachBottom() {
		  var that = this;
		  if (that.attentionNode.page == that.attentionNode.page_number) {
				userAttention.show_tips('没有更多数据了')
		    return;
		  }
		  that.attentionNode.page += 1;
		  // 客户经理列表加载
		  this.getList()
		},
		// 分享
		onShareAppMessage() {
			// let shareData = {
			// 	title: '',
			// 	path: `pages/index/index`,
			// 	imageUrl: ''
			// }
			return userAttention.onShareAppMessage({});
		}
	}
